import {Component, Input, Output, ElementRef, EventEmitter} from '@angular/core';
import {CORE_DIRECTIVES} from '@angular/common';
import {Control} from '@angular/common';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/observable/fromEvent';
@Component({
  selector: 'input-delay',
  template: '<input  [ngFormControl]="debounceSearch" type="text" class="form-control" [placeholder]="placeholder" >',
  outputs: ['value'],
  inputs:['placeholder']

})
export class inputDelayComponent {
  value=new EventEmitter<string>();
  debounceSearch = new Control();
  constructor() {
    this.debounceSearch.valueChanges
      .debounceTime(500)
      .subscribe(newValue => this.value.emit(newValue));
  }
}
